import React, { useMemo } from 'react';
import { Formik, Form } from 'formik';
import request from 'utils/request';
import Input from 'components/Input';
import './style.scss';
import { useHistory } from 'react-router-dom';
import { useSetRecoilState } from 'recoil';
import { userState, IUser } from 'recoil/user';

interface SignInProps {}

interface FormValues {
  username: string;
  password: string;
}

interface SignInResponse extends IUser {
  token: string;
}

const SignIn: React.FC<SignInProps> = () => {
  const setUser = useSetRecoilState(userState);
  const history = useHistory();

  const initialValues: FormValues = useMemo(
    () => ({
      username: '',
      password: '',
    }),
    []
  );

  return (
    <div className='lanting-signin'>
      <Formik
        onSubmit={(values, { setSubmitting }) => {
          console.log(values);

          request
            .post<any, SignInResponse>('/auth/signin', {
              ...values,
            })
            .then(({ token, ...user }) => {
              setUser(user);
              localStorage.setItem('lanting-token', token);
              history.replace('/');
            })
            .finally(() => {
              setSubmitting(false);
            });
        }}
        initialValues={initialValues}
      >
        {({ values, handleChange, handleSubmit, isSubmitting }) => (
          <Form onSubmit={handleSubmit}>
            <div className='lanting-signin-username'>
              <label htmlFor='username'>Username</label>
              <Input
                autoComplete='off'
                name='username'
                id='username'
                value={values.username}
                onChange={handleChange}
              />
            </div>
            <div className='lanting-signin-password'>
              <label htmlFor='password'>Password</label>
              <Input
                autoComplete='off'
                name='password'
                id='password'
                value={values.password}
                onChange={handleChange}
              />
            </div>
            <div>
              <button type='submit' disabled={isSubmitting}>
                Sign In
              </button>
            </div>
          </Form>
        )}
      </Formik>
    </div>
  );
};

export default SignIn;
