import React, { useMemo, useCallback } from 'react';
import classNames from 'classnames';
import './style.scss';
import { Link, useLocation } from 'react-router-dom';
import Icon from 'components/Icon';
import { useRecoilState } from 'recoil';
import { themeState } from 'recoil/theme';

interface NavProps {}

const Nav: React.FC<NavProps> = () => {
  const [theme, setTheme] = useRecoilState(themeState);
  const { pathname } = useLocation();

  const isEditPage = useMemo(() => {
    return pathname === '/edit';
  }, [pathname]);

  const toggleTheme = () => {
    if (theme === 'dark') {
      setTheme('light');
    } else {
      setTheme('dark');
    }
  };

  return (
    <div className={classNames(`lanting-nav`)}>
      <div className='lanting-nav-inner'>
        <div className='lanting-nav-title'>
          <Link to='/' className='lanting-nav-link'>
            <h2 className='lanting-nav-name'>ÂÖ∞‰∫≠</h2>
          </Link>
          <div className='lanting-nav-bio'>ÊµÅËßûÊõ≤Ê∞¥</div>
        </div>
        {!isEditPage && (
          <Link className='lanting-nav-edit' to='/edit'>
            <Icon>edit</Icon>
          </Link>
        )}
        <span onClick={toggleTheme} className='lanting-nav-theme'>
          {theme === 'dark' ? 'üåô' : '‚òÄÔ∏è'}
        </span>
      </div>
    </div>
  );
};

export default Nav;
